body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 30px 40px;
  margin: 40px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  box-sizing: border-box;
  width: 200px;
  transition-duration: 1s;
}

.photo {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.people__image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition-duration: 1s;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  transition: transform 1s ease-in-out;
  display: flex;
  position: absolute;
  right: 10px;
  transform: translate(0);
}

.details {
  text-align: center;
}

.name {
  letter-spacing: 0%;
  line-height: 100%;
}

.sport, .pay {
  font-weight: 400;
  font-size: px;
  letter-spacing: 0%;
  text-align: center;
  line-height: 100%;
}

.extra {
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s ease-in-out;
}

.person:hover .extra {
  visibility: visible;
  opacity: 1;
  font-size: 18px;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  transition-duration: 1s;
  transform: scale(1.2);
}

.people__image:hover {
  border-radius: 60px;
  transition-duration: 1s;
}
